.country{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    transition: all 1s ease-in-out;
    cursor: pointer;
    background-color: aquamarine;
}

.country:hover{
    transform: scale(1.1);
}

.country > img {
    width: 33%;
    height: auto;
}

.country > h2 {
    margin: 5px 0;
}

.describe {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    justify-items: center;
    width: 95%;
}